.product {
    background: var(--clr-white);
    border-radius: 10px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    /*  */
    transition: all 0.3s linear 0s;
}
.product img {
    width: 100%;
    height: 13rem;
    object-fit: cover;
}
.product h4 {
    margin-top: 1rem;
}

.product button {
    margin-bottom: 1rem;
    background: var(--clr-primary-5);
    border-radius: var(--radius);
    border-color: transparent;
    color: var(--clr-white);
    padding: 0.25rem 0.5rem;
    text-transform: capitalize;
    cursor: pointer;
}

.growServicio:hover {
    transform: scale(1.04);
    animation-delay: 0.5s;
    box-shadow: rgba(47, 135, 173, 0.75) 0px 5px 15px;
}

@media screen and (max-width: 420px) {
    .growServicio:hover {
        transform: scale(1);
        animation-delay: 0;
        box-shadow: rgba(47, 135, 173, 0.35) 0px 5px 15px;
    }
}
